import React, { useCallback, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './SignUp.module.css';
import loginStyles from './Login.module.css';
import axios from 'axios';

// Button  import
import Button from 'react-bootstrap/Button';

const SignUp = () => {
  const navigate = useNavigate();

  // useState 
  const [msg, setMsg] = useState('');
  const [formData, setFormData] = useState({
    userId: '',
    userPw: '',
  });
  const { userId, userPw } = formData;

  //      
  const handleChange = useCallback((e) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const res = await axios.post('/signup', formData);

      if (res.status !== 200) {
        //     
        throw new Error(' ');
      }

      alert(' ');
      navigate('/');
    } catch (err) {
      // setMsg    
      setMsg(err.message);
    }
    console.log('formData : ', formData);
  };

  const handleCancel = () => {
    navigate('/');
  };

  return (
    <div className={styles.page}>
      <header className={styles.header}>
        <h2 className={styles.signUptitle}></h2>
      </header>
      <div className={styles.InputWrapper}>
        <label>
          <div className={styles.signupList}></div>
          <div className={styles.idBox}>
            <input
              type="text"
              placeholder=",  4 "
              value={userId}
              name="userId"
              onChange={handleChange}
              className={loginStyles.input}
            />
          </div>
        </label>
        <br />
        <label>
          <div className={styles.signupList}></div>
          <input
            type="password"
            placeholder=", ,   8~20"
            value={userPw}
            name="userPw"
            onChange={handleChange}
            className={loginStyles.input}
          />
        </label>
      </div>
      <br />

      <div className={styles.signupBtnWrapper}>
        <Button type="submit" onClick={handleSubmit} className={styles.signupBtn}>
          
        </Button>
        <Button type="submit" onClick={handleCancel} className={styles.signupBtn}>
          
        </Button>
      </div>
      {/*      */}
      {msg && <p>{msg}</p>}
    </div>
  );
};

export default SignUp;
