import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import StorageStyle from './Storage.module.css';
import Nav from '../../Nav';
import axios from 'axios';

const StorageSelect = () => {
  const navigate = useNavigate();
  const [memoList, setMemoList] = useState([]);

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem('user'));
    if (user && user.username) {
      axios
        .get(`/memos/${user.username}`)
        .then((response) => {
          setMemoList(response.data);
        })
        .catch((error) => {
          console.error('Error fetching memos:', error);
        });
    }
  }, []);

  const handleEdit = (index) => {
    navigate(`/edit/${index}`);
  };

  const path = process.env.PUBLIC_URL;

  return (
    <>
      <div className={StorageStyle.storageWrapper}>
        <div className={StorageStyle.selectListWrapper}>
          <header className={StorageStyle.header}>
            <Link to="/add">
              <button className={StorageStyle.plusBtn}>
                <img src={path + '/images/fridge_add.svg'} alt="Add" />
              </button>
            </Link>
            <h2 className={StorageStyle.myTitle}> </h2>
            <Link to="/mypage">
              <button className={StorageStyle.myPageBtn}>
                <img src={path + '/images/myPage.svg'} alt="My Page" />
              </button>
            </Link>
          </header>
          {memoList.length > 0 ? (
            <ul className={StorageStyle.memoList}>
              {memoList.map((item, index) => (
                <li key={index}>
                  <div className={StorageStyle.memoItem}>
                    <div className={StorageStyle.fridgeNameWrapper}>
                      <Link to="/food" style={{ textDecoration: 'none' }}>
                        <strong className={StorageStyle.fridgeName}>{item.fridgeName}</strong>
                      </Link>
                      <button className={StorageStyle.editBtn} onClick={() => handleEdit(index)}>
                        <img src={path + '/images/fridge_setting.svg'} alt="Edit" />
                      </button>
                    </div>
                    <img src={path + '/images/fridge_magnet.svg'} className={StorageStyle.orangeMagnet} alt="Magnet" />
                    <div className={StorageStyle.memoBg}>
                      <p className={StorageStyle.memoContent}>{item.memo}</p>
                    </div>
                  </div>
                </li>
              ))}
            </ul>
          ) : (
            <div className={StorageStyle.emptyWrapper}>
              <h2 className={StorageStyle.emptyMemoFirst}>  </h2>
              <p className={StorageStyle.emptyMemoSecond}> </p>
              <p className={StorageStyle.emptyMemoThird}>   :)</p>
            </div>
          )}
        </div>
        <Nav />
      </div>
    </>
  );
};

export default StorageSelect;
